<template>
  <div>

    <div class="container section">
      <div class="row">
        <div class="col s12 m8 offset-m2">

          <div class="login card-panel white black-text center">

            <h3>Login</h3>

            <form>

              <div class="input-field">
                <i class="material-icons prefix">email</i>
                <input type="text" class="black-text text-lighten-3" id="email" v-model="email">
                <label class="blue-text text-lighten-3" for="email">Email</label>
              </div>

              <div class="input-field">
                <i class="material-icons prefix">lock</i>
                <input type="password" class="black-text text-lighten-3" id="password" v-model="password">
                <label class="blue-text text-lighten-3" for="password">Password</label>
              </div>

              <button v-on:click="login"
                      class="btn btn-large grey lighten-4 black-text">
                Login
              </button>

            </form>

          </div>

        </div>
      </div>
    </div>

  </div>
</template>

<script>
import firebase from 'firebase';

export default {
  name: 'login',

  data: function () {
    return {
      email: '',
      password: ''
    };
  },

  methods: {
    login: function (e) {

      firebase.auth()

        .signInWithEmailAndPassword( this.email, this.password )

        .then(user => {

            var user = firebase.auth().currentUser;

            alert(`You area logged in as ${user.email}`);

            // this.$router.push('/');
            this.$router.go({
              path: this.$router.path
            });

          },
          err => {
            console.log(err.code);
            alert(err.message);
          });

      e.preventDefault();

    }

  }

}
</script>

<style scoped>

</style>



