<template>
  <nav>

    <div class="nav-wrapper red">
      <div class="container">

        <router-link to="/" class="brand-logo">
          <i class="material-icons">cloud</i>
          Bengi
        </router-link>

        <ul class="right hide-on-med-and-down">

          <li v-if="isLoggedIn">
            <span class="email white-text text-lighten-3">
              {{ currentUser }}
            </span>
          </li>

          <li v-if="isLoggedIn">
            <router-link to="/">DashBoard</router-link>
          </li>
          <li v-if="!isLoggedIn">
            <router-link to="/login">Login</router-link>
          </li>
          <li v-if="isLoggedIn">
            <router-link to="/register">Register</router-link>
          </li>
          <li v-if="isLoggedIn">
            <button v-on:click="logout" class="btn black waves-effect waves-light btn-small">
              Logout
            </button>
          </li>
        </ul>


      </div>
    </div>

  </nav>
</template>


<script>
import firebase from 'firebase';

export default {

  name: 'navbar',

  data () {
    return {
      isLoggedIn: false,
      currentUser: false
    }
  },

  created () {

    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }

  },

  methods: {

    logout: function () {

      firebase.auth()
        .signOut()
        .then(() => {

          // this.$router.push('/login');
          this.$router.go({
            path: this.$router.path
          });

        })
    }

  }

}
</script>

<style scoped>

  .email {
    padding-right: 10px;
  }

</style>



